.course-card-container {
  border-top: 1px solid $dark-grey;
}
.course-card {
  display: flex;
  align-items: top;
  width: 80%;
  margin: 50px auto;
}
.course-card-left {
  width: 30%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.course-card-left > .course-buttons {
  width: 70%;
  display: flex;
  justify-content: space-between;
}
.course-buttons > .btn {
  margin-top: 10px;
  width: 50px;
}
.course-card-right {
  width: 70%;
  display: flex;
  flex-direction: column;
}
.course-card-right > h3 {
  font-size: 25px;
  margin: 5px 2%;
}
.course-card-right > .course-info {
  border: 2px solid $dark-grey;
  width: calc(98% - 9px);
  padding-left: 12%;
  margin-left: -10%;
  padding-right: 5px;
  font-family: 'Montserrat Ultra Light';
  font-size: 12px;
  flex: 1;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}
.course-info-bit {
  display: flex;
}
.info-bit-content {
  display: flex;
  flex-direction: column;
  margin-left: 3px;
}



